.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.title {
  font-size: calc(50px + 2vmin);
}

.text {
  font-size: calc(5px + 2vmin);
}

.link {
  color: #61dafb;
  text-decoration: none;
  display: inline;
}
